import React, { useState, useEffect } from "react";
import Header from "./components/Header.js";
import Question from "./components/Question.js";
import Answer from "./components/Answer.js";
import axios from "axios";
import "./App.css";

function App() {
  const [pages, setPages] = useState({});
  const [loading, setLoading] = useState(true);
  const [ansArray, setAns] = useState([]);
  const [eve, setEve] = useState(-1);
  const [que, setQues] = useState("");
  const [questionNumber, setQuestion] = useState(0);
  const [newQ, setNewq] = useState(0);
  const [canswer, setCanswer] = useState("");
  const [showAnswer, setShowAnswer] = useState(false);

  useEffect(() => {
    axios
      .get(
        "https://opentdb.com/api.php?amount=10&category=15&difficulty=easy&type=multiple"
      )
      .then((res) => {
        console.log(res);
        setPages(res.data);
        setLoading(false);
      });
  }, [newQ]);

  function eveHandler() {
    setEve((prevState) => prevState + 1);
    setAns([
      pages.results[0].correct_answer,
      pages.results[0].incorrect_answers[0],
      pages.results[0].incorrect_answers[1],
      pages.results[0].incorrect_answers[2],
    ]);
    setQues(pages.results[0].question);
    setCanswer(pages.results[questionNumber].correct_answer);
    setQuestion(1);
    console.log();
  }

  function onClickHandler(event) {
    setShowAnswer(true);
  }

  function onNextHandler() {
    setQuestion((prev) => prev + 1);
    const Arr = pages.results[questionNumber];
    let newArr = [];
    newArr.push(Arr.correct_answer);
    newArr = newArr.concat(Arr.incorrect_answers);
    newArr.sort(() => Math.random() - 0.5);
    setAns(newArr);
    setQues(Arr.question);
    setCanswer(Arr.correct_answer);
    setShowAnswer(false);
    if (questionNumber === 9) {
      setNewq((pre) => pre + 1);
      setQuestion(0);
    }
  }

  return (
    <div className="App">
      <Header />
      {loading ? (
        "loading"
      ) : (
        <Question eveHandler={eveHandler} eve={eve} results={que} />
      )}
      <Answer
        showAnswer={showAnswer}
        onNextHandler={onNextHandler}
        onClickHandler={onClickHandler}
        ansArray={ansArray}
        canswer={canswer}
        eve={eve}
      />
    </div>
  );
}

export default App;
