import React from "react";

function Answer(props) {
  function display() {
    if (props.eve === -1) {
      return "start the quiz, no time limit";
    } else {
      return (
        <div>
          <button className="ans__button" onClick={props.onClickHandler}>
            {props.ansArray[0]}
          </button>
          <button className="ans__button" onClick={props.onClickHandler}>
            {props.ansArray[1]}
          </button>
          <button className="ans__button" onClick={props.onClickHandler}>
            {props.ansArray[2]}
          </button>
          <button className="ans__button" onClick={props.onClickHandler}>
            {props.ansArray[3]}
          </button>
          <br />
          <br />
          <button onClick={props.onNextHandler}>Next</button>
          <br />
          {props.showAnswer ? (
            <input
              type="text"
              className="text"
              placeholder="Answer"
              value={props.canswer}
              readOnly
            />
          ) : (
            "click options for answer"
          )}
        </div>
      );
    }
  }

  return <div>{display()}</div>;
}

export default Answer;
